<!-- src/components/PageTwo.vue -->
<template>
    <div ref="chart" style="width: 100%; height: 400px;margin-top: 40px;"></div>

</template>
  
  <script>
  import * as echarts from 'echarts';
  import { mapState } from 'vuex';
  
  export default {
    name: 'PageTwo',
    computed: {
      ...mapState(['ToDoItems']),
    },
    mounted() {
      this.initChart();
    },
    methods: {
      initChart() {
        const chart = echarts.init(this.$refs.chart);
        const numberFinishedItems = this.ToDoItems.filter(item => item.done).length;
        const totalItems = this.ToDoItems.length;
        const option = {
          title: {
            text: '任务完成数据',
            subtext: `${numberFinishedItems} out of ${totalItems} items completed`,
            left: 'center'
          },
          tooltip: {
            trigger: 'item',
            formatter: (params) => {
              const percentage = (params.value / totalItems) * 100;
              return `Label: ${params.name}<br/>Done: ${percentage.toFixed(2)}%`;
            }
          },
          legend: {
            orient: 'vertical',
            left: 'left'
          },
          series: [
            {
              name: '任务完成情况',
              type: 'pie',
              radius: '50%',
              data: [
                { value: numberFinishedItems, name: '已完成' },
                { value: totalItems - numberFinishedItems, name: '未完成' }
              ],
              emphasis: {
                itemStyle: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
              }
            }
          ]
        };
        chart.setOption(option);
      }
    }
  };
  </script>
  
  <style scoped>
  /* 你可以在这里添加样式 */
  </style>