<!-- src/components/PageFive.vue -->
<template>
    <div ref="chart" style="width: 100%; height: 400px;margin-top: 40px;"></div>
  </template>
  
  <script>
  import * as echarts from 'echarts';
  import { mapState } from 'vuex';
  
  export default {
    name: 'PageFive',
    computed: {
      ...mapState(['ToDoItems']),
    },
    mounted() {
      this.initChart();
    },
    methods: {
      initChart() {
        const chart = echarts.init(this.$refs.chart);
        const axisData = this.ToDoItems.map(item => item.label);
        const data = this.ToDoItems.map(item => ({
          value: item.done ? 1 : -1,
          itemStyle: {
            color: item.done ? 'green' : 'red'
          }
        })); // 根据 done 属性设置值和颜色
        const links = data.map((_, i) => ({
          source: i,
          target: i + 1
        }));
        links.pop();
  
        const option = {
          title: {
            text: '任务完成情况',
            left: 'center'
          },
          tooltip: {
            trigger: 'item',
            formatter: function (params) {
              const status = params.value === 1 ? '完成' : '未完成';
              return `任务: ${params.name}<br/>状态: ${status}`;
            }
          },
          grid: {
            left: '10%', // 调整图表左边距
            right: '20%', // 调整图表右边距
            top: 80,
            bottom: 100 // 调整图表底部边距以留出足够的空间
          },
          legend: {
            orient: 'vertical',
            left: 'left'
          },
          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: axisData,
            axisLine: { show: false },
            axisLabel: { show: false },
            axisTick: { show: false },
            splitLine: { show: false }
          },
          yAxis: {
            type: 'value',
            min: -1,
            max: 1,
            interval: 1,
            axisLabel: {
              formatter: function (value) {
                return value === -1 ? '未完成' : value === 1 ? '完成' : '0';
              }
            }
          },
          series: [
            {
              type: 'graph',
              layout: 'none',
              coordinateSystem: 'cartesian2d',
              symbolSize: 40,
              label: {
                show: true
              },
              edgeSymbol: ['circle', 'arrow'],
              edgeSymbolSize: [4, 10],
              data: data,
              links: links,
              lineStyle: {
                color: '#2f4554'
              }
            }
          ]
        };
        chart.setOption(option);
      }
    }
  };
  </script>
  
  <style scoped>
  /* 你可以在这里添加样式 */
  </style>