<!-- src/components/PageOne.vue -->
<template>
    <div ref="chart" style="width: 100%; height: 400px; margin-top: 40px;"></div>
</template>
  
  <script>
  import * as echarts from 'echarts';
  import { mapState } from 'vuex';
  
  export default {
    name: 'PageOne',
    computed: {
      ...mapState(['ToDoItems']),
    },
    data() {
      return {
        chart: null,
      };
    },
    mounted() {
      this.initChart();
    },
    methods: {
      initChart() {
        this.chart = echarts.init(this.$refs.chart);
        this.updateChart();
      },
      updateChart() {
        const colors = this.ToDoItems.map((item, index) => {
          const hue = index * (360 / this.ToDoItems.length);
          return `hsl(${hue}, 100%, 50%)`;
        });
  
        const option = {
          title: {
            text: 'To-Do Items',
            left: 'center',
            
            
          },
          tooltip: {
            trigger: 'item',
            formatter: (params) => {
              const item = this.ToDoItems.find(i => i.label === params.name);
              return `Label: ${item.label}<br/>Done: ${item.done}`;
            }
          },
          legend: {
            orient: 'vertical',
            left: 'left'
          },
          series: [
            {
              name: 'To-Do Items',
              type: 'pie',
              radius: '50%',
              data: this.ToDoItems.map((item, index) => ({
                value: 1, // 每个扇形图的面积一样大
                name: item.label,
                itemStyle: {
                  color: colors[index] // 分配不同的颜色
                }
              })),
              emphasis: {
                itemStyle: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
              }
            }
          ]
        };
        this.chart.setOption(option);
      }
    }
  };
  </script>
  
  <style scoped>
  /* 你可以在这里添加样式 */
  </style>