<!-- src/components/NavigatorFourPage.vue -->
<template>
    <div>
      <to-do-form @todo-added="addToDo"></to-do-form>
      <h2 id="list-summary" ref="listSummary" tabindex="-1" class="list-summary">{{ listSummary }}</h2>
      <ul aria-labelledby="list-summary" class="stack-large">
        <li v-for="item in ToDoItems" :key="item.id">
          <to-do-item
            :label="item.label"
            :done="item.done"
            :id="item.id"
            @checkbox-changed="updateDoneStatus(item.id)"
            @item-deleted="deleteToDo(item.id)"
            @item-edited="editToDo({ toDoId: item.id, newLabel: $event })">
          </to-do-item>
        </li>
      </ul>
    </div>
  </template>
  
  <script>
  import { mapState, mapGetters, mapMutations } from 'vuex';
  import ToDoItem from './ToDoItem.vue';
  import ToDoForm from './ToDoForm.vue';
  
  export default {
    name: 'NavigatorFourPage',
    components: {
      ToDoItem,
      ToDoForm,
    },
    computed: {
      ...mapState(['ToDoItems']),
      ...mapGetters(['listSummary']),
    },
    methods: {
      ...mapMutations(['addToDo', 'updateDoneStatus', 'deleteToDo', 'editToDo']),
    },
  };
  </script>

<style scoped>
.list-summary {
  margin-top: 60px; /* 设置与上面的距离 */
}
</style>