// src/store.js
import { createStore } from 'vuex';
import uniqueId from 'lodash.uniqueid';

export default createStore({
  state: {
    ToDoItems: JSON.parse(localStorage.getItem('ToDoItems')) || [
      { id: uniqueId('todo-'), label: 'Learn Vue', done: false },
      { id: uniqueId('todo-'), label: 'Create a Vue project with the CLI', done: true },
      { id: uniqueId('todo-'), label: 'Have fun', done: true },
      { id: uniqueId('todo-'), label: 'Create a to-do list', done: false },
    ],
  },
  mutations: {
    setToDoItems(state, items) {
      state.ToDoItems = items;
      localStorage.setItem('ToDoItems', JSON.stringify(state.ToDoItems));
    },
    addToDo(state, toDoLabel) {
      state.ToDoItems.push({
        id: uniqueId('todo-'),
        label: toDoLabel,
        done: false,
      });
      localStorage.setItem('ToDoItems', JSON.stringify(state.ToDoItems));
    },
    updateDoneStatus(state, toDoId) {
      const toDoToUpdate = state.ToDoItems.find((item) => item.id === toDoId);
      if (toDoToUpdate) {
        toDoToUpdate.done = !toDoToUpdate.done;
        localStorage.setItem('ToDoItems', JSON.stringify(state.ToDoItems));
      }
    },
    deleteToDo(state, toDoId) {
      const itemIndex = state.ToDoItems.findIndex((item) => item.id === toDoId);
      if (itemIndex !== -1) {
        state.ToDoItems.splice(itemIndex, 1);
        localStorage.setItem('ToDoItems', JSON.stringify(state.ToDoItems));
      }
    },
    editToDo(state, { toDoId, newLabel }) {
      const toDoToEdit = state.ToDoItems.find((item) => item.id === toDoId);
      if (toDoToEdit) {
        toDoToEdit.label = newLabel;
        localStorage.setItem('ToDoItems', JSON.stringify(state.ToDoItems));
      }
    },
  },
  getters: {
    listSummary(state) {
      const numberFinishedItems = state.ToDoItems.filter((item) => item.done).length;
      return `${numberFinishedItems} out of ${state.ToDoItems.length} items completed`;
    },
  },
});