// src/router.js
import { createRouter, createWebHistory } from 'vue-router';
import HomePage from './components/HomePage.vue';
import PageOne from './components/PageOne.vue';
import PageTwo from './components/PageTwo.vue';
import PageThree from './components/PageThree.vue';
import PageFour from './components/PageFour.vue';
import PageFive from './components/PageFive.vue';
import NavigatorFourPage from './components/NavigatorFourPage.vue';

const routes = [
  { path: '/', component: HomePage },
  { path: '/page-one', name: 'PageOne', component: PageOne, props: true },
  { path: '/page-two', name: 'PageTwo', component: PageTwo, props: true },
  { path: '/page-three', name: 'PageThree', component: PageThree },
  { path: '/page-four', name: 'PageFour', component: PageFour },
  { path: '/page-five', name: 'PageFive', component: PageFive },
  { path: '/navigator-four', component: NavigatorFourPage },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;