<template>
  <form @submit.prevent="onSubmit">
    <h2 class="label-wrapper">
      <label for="new-todo-input" class="label__lg">
        
      </label>
    </h2>
    <div class="input-button-wrapper">
      <el-input
        v-model="label"
        style="width: 240px"
        maxlength="30"
        placeholder="Please input --what needs to be done?"
        show-word-limit
        type="text"
        id="new-todo-input"
        name="new-todo"
        autocomplete="off"
        class="input__lg"
      />
      <el-button type="primary" @click="onSubmit" class="add-button">
        Add
        <el-icon class="el-icon--right"><Upload /></el-icon>
      </el-button>
    </div>
  </form>
</template>

<script>
import { Upload } from '@element-plus/icons-vue';
import { ElIcon, ElInput } from 'element-plus';

export default {
  components: {
    Upload,
    ElIcon,
    ElInput
  },
  data() {
    return {
      label: "",
    };
  },
  methods: {
    onSubmit() {
      if (this.label === "") {
        return;
      }
      this.$emit("todo-added", this.label);
      this.label = "";
    },
  },
};
</script>

<style>


.input-button-wrapper {
  display: flex;
  align-items: center;
  margin-top: 40px; /* 设置输入框距离上方的宽度 */
}

.input-button-wrapper .el-input {
  flex: 1;
}

.input-button-wrapper .el-button {
  margin-left: 10px;
  height: 41px; /* 设置按钮高度 */
  display: flex;
  align-items: center;
  margin-top: -10px; /* 将按钮向上移动 */
}

.input__lg .el-input__inner {
  height: 40px; /* 设置输入框高度 */
}

.input__lg::placeholder {
  font-size: 1px; /* 调小占位符字体 */
}

</style>