<!-- src/components/PageThree.vue -->
<template>
  <div ref="chart" style="width: 100%; height: 400px;margin-top: 40px;"></div>
</template>

<script>
import * as echarts from 'echarts';
import { mapState } from 'vuex';

export default {
  name: 'PageThree',
  computed: {
    ...mapState(['ToDoItems']),
  },
  mounted() {
    this.initChart();
  },
  methods: {
    initChart() {
      const chart = echarts.init(this.$refs.chart);
      const option = {
        title: {
          text: '任务完成情况',
          left: 'center',
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          }
        },
        grid: {
          top: 80,
          bottom: 30
        },
        xAxis: {
          type: 'value',
          position: 'top',
          splitLine: {
            lineStyle: {
              type: 'dashed'
            }
          }
        },
        yAxis: {
          type: 'category',
          axisLine: { show: false },
          axisLabel: { show: false },
          axisTick: { show: false },
          splitLine: { show: false },
          data: this.ToDoItems.map(item => item.label)
        },
        series: [
          {
            name: '未完成',
            type: 'bar',
            stack: '总量',
            label: {
              show: true,
              position: 'center',
              formatter: '{b}'
            },
            data: this.ToDoItems.map(item => item.done ? 0 : -1)
          },
          {
            name: '已完成',
            type: 'bar',
            stack: '总量',
            label: {
              show: true,
              position: 'center',
              formatter: '{b}'
            },
            data: this.ToDoItems.map(item => item.done ? 1 : 0)
          }
        ]
      };
      chart.setOption(option);
    }
  }
};
</script>

