<!-- src/components/PageFour.vue -->
<template>
    <div ref="chart" style="width: 100%; height: 400px;margin-top: 40px;"></div>
  </template>
  
  <script>
  import * as echarts from 'echarts';
  import { mapState } from 'vuex';
  
  export default {
    name: 'PageFour',
    computed: {
      ...mapState(['ToDoItems']),
    },
    mounted() {
      this.initChart();
    },
    methods: {
      initChart() {
        const chart = echarts.init(this.$refs.chart);
        const option = {
          grid: {
            left: '10%', // 调整图表左边距
            right: '20%', // 调整图表右边距
            top: 80,
            bottom: 100 // 调整图表底部边距以留出足够的空间
          },

          title: {
            text: '任务完成情况',
            left: 'center',
         },

          xAxis: {
            type: 'category',
            name: '任务事件',
            data: this.ToDoItems.map(item => item.label),
            axisLabel: {
              rotate: 45, // 倾斜角度
              formatter: function (value) {
                return value.length > 20 ? value.substring(0, 20) + '...' : value;
              },
              overflow: 'truncate'
            }
          },
          yAxis: {
            type: 'value',
            name: '完成进度',
            min: 0,
            max: 1,
            interval: 0.2,
            axisLabel: {
              formatter: function (value) {
                return `${value * 100}%`;
              }
            }
          },
          series: [
            {
              data: this.ToDoItems.map(item => item.done ? 1 : 0),
              type: 'bar',
              showBackground: true,
              backgroundStyle: {
                color: 'rgba(180, 180, 180, 0.2)'
              }
            }
          ]
        };
        chart.setOption(option);
      }
    }
  };
  </script>
  
  <style scoped>
  /* 你可以在这里添加样式 */
  </style>